
.form-label {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  font-size: 1rem;
  font-weight: 600;
  color: var(--title-color);
  margin-bottom: var(--spacing-xs);
  letter-spacing: 0.3px;
  cursor: pointer;
  transition: color var(--transition-fast);

  &:hover {
    color: var(--accent-color);
  }

  .required-mark {
    color: var(--error-color);
    font-weight: 700;
    font-size: 1.1rem;
    line-height: 1;
  }
}
