
.destined-one-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: var(--spacing-lg);
  padding: var(--spacing-md);
}

.empty-message {
  grid-column: 1 / -1;
  text-align: center;
  padding: var(--spacing-2xl);
  color: var(--text-light);
  font-size: 1.1rem;
}

.destined-one-card {
  background: var(--card-bg);
  border: 2px solid var(--border-color);
  border-radius: var(--radius-lg);
  padding: var(--spacing-md);
  cursor: pointer;
  transition: all var(--transition-fast);

  &:hover:not(.disabled) {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
    border-color: var(--accent-color);
  }

  &.selected {
    border-color: var(--accent-color);
    background: rgba(212, 175, 55, 0.15);
    box-shadow: var(--shadow-md);
  }

  &.disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &.expanded {
    .card-body {
      max-height: 10000px;
      opacity: 1;
    }
  }
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-sm);
  padding-bottom: var(--spacing-sm);
  border-bottom: 2px solid var(--border-color);

  .item-name {
    font-size: 1.2rem;
    color: var(--title-color);
    margin: 0;
    flex: 1;
  }

  .header-actions {
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);
  }

  .item-cost {
    font-weight: 600;
    color: var(--accent-color);
    font-size: 1rem;
  }

  .expand-btn {
    padding: 2px var(--spacing-sm);
    background: var(--input-bg);
    border: 1px solid var(--border-color);
    border-radius: var(--radius-sm);
    cursor: pointer;
    font-size: 0.8rem;
    color: var(--text-color);
    transition: all var(--transition-fast);

    &:hover {
      background: var(--accent-color);
      color: var(--primary-bg);
      border-color: var(--accent-color);
    }
  }
}

.card-summary {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--spacing-xs);
  margin-bottom: var(--spacing-sm);

  .summary-row {
    display: flex;
    gap: var(--spacing-xs);
    font-size: 0.85rem;

    .label {
      color: var(--text-light);
      white-space: nowrap;
    }

    .value {
      color: var(--text-color);
      font-weight: 500;
    }
  }
}

.card-body {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: all var(--transition-normal);
}

.info-section {
  padding: var(--spacing-sm) 0;
  border-bottom: 1px solid var(--border-color-light);

  &:last-child {
    border-bottom: none;
  }
}

.info-row {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: var(--spacing-sm);
  font-size: 0.9rem;
  line-height: 1.6;
  margin-bottom: var(--spacing-xs);

  .label {
    color: var(--text-light);
    font-weight: 500;
    white-space: nowrap;
  }

  .value {
    color: var(--text-color);
    word-break: break-word;
  }
}

.info-block {
  margin-bottom: var(--spacing-sm);

  &:last-child {
    margin-bottom: 0;
  }
}

.block-title {
  font-weight: 600;
  color: var(--title-color);
  margin-bottom: var(--spacing-xs);
  font-size: 0.95rem;
}

.block-content {
  color: var(--text-color);
  line-height: 1.6;
  font-size: 0.85rem;
}

.attributes {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
  gap: var(--spacing-xs);
}

.attr-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: 4px var(--spacing-xs);
  background: rgba(212, 175, 55, 0.05);
  border-radius: var(--radius-sm);

  .attr-label {
    color: var(--text-light);
    font-size: 0.8rem;
  }

  .attr-value {
    color: var(--accent-color);
    font-weight: 600;
    font-size: 0.85rem;
  }
}

.equipment-list,
.skills-list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.equipment-item,
.skill-item {
  padding: var(--spacing-sm);
  background: rgba(0, 0, 0, 0.02);
  border-left: 3px solid var(--accent-color);
  border-radius: var(--radius-sm);
}

.equip-name,
.skill-name {
  font-weight: 600;
  color: var(--title-color);
  margin-bottom: var(--spacing-xs);
  font-size: 0.9rem;
}

.equip-info,
.skill-tag,
.skill-consume {
  font-size: 0.8rem;
  color: var(--text-light);
  margin-bottom: var(--spacing-xs);
}

.equip-desc,
.skill-effect,
.skill-desc {
  font-size: 0.8rem;
  color: var(--text-color);
  line-height: 1.5;
  margin-bottom: var(--spacing-xs);

  &:last-child {
    margin-bottom: 0;
  }
}

.skill-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-xs);

  .skill-type {
    font-size: 0.75rem;
    padding: 2px var(--spacing-xs);
    background: var(--accent-color);
    color: var(--primary-bg);
    border-radius: var(--radius-sm);
  }
}

.stairway-content {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.stairway-section {
  padding: var(--spacing-xs);
  background: rgba(0, 0, 0, 0.02);
  border-radius: var(--radius-sm);
}

.stairway-label {
  font-weight: 600;
  color: var(--title-color);
  margin-bottom: var(--spacing-xs);
  font-size: 0.85rem;
}

.stairway-item {
  font-size: 0.8rem;
  color: var(--text-color);
  margin-bottom: var(--spacing-xs);
  line-height: 1.5;

  &:last-child {
    margin-bottom: 0;
  }
}

.stairway-desc {
  font-size: 0.8rem;
  color: var(--text-light);
  line-height: 1.5;
  margin-top: var(--spacing-xs);
}

.comment {
  margin-top: var(--spacing-sm);
  padding: var(--spacing-sm);
  background: rgba(212, 175, 55, 0.1);
  border-left: 3px solid var(--accent-color);
  border-radius: var(--radius-sm);
  font-style: italic;
  color: var(--text-color);
  font-size: 0.85rem;
  line-height: 1.6;
}

@media (max-width: 768px) {
  .destined-one-list {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
    padding: var(--spacing-sm);
  }

  .destined-one-card {
    padding: var(--spacing-sm);
  }

  .card-header {
    flex-wrap: wrap;
    gap: var(--spacing-xs);

    .item-name {
      font-size: 1.1rem;
      flex-basis: 100%;
    }

    .header-actions {
      flex-basis: 100%;
      justify-content: space-between;
    }

    .item-cost {
      font-size: 0.9rem;
    }
  }

  .card-summary {
    grid-template-columns: 1fr;
  }

  .attributes {
    grid-template-columns: repeat(2, 1fr);
  }
}
