
.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);

  &.full-width {
    grid-template-columns: 1fr;
  }
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);

  label {
    font-weight: 600;
    color: var(--accent-color);
    font-size: 1rem;
    display: flex;
    align-items: center;
    gap: var(--spacing-xs);
    letter-spacing: 0.5px;

    &.required::after {
      content: '*';
      color: #ff6b6b;
      font-weight: bold;
      margin-left: 2px;
    }
  }
}

.level-input-group {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);

  .number-input-wrapper {
    flex: 1;
  }

  .level-indicator {
    padding: var(--spacing-sm) var(--spacing-md);
    background: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: var(--radius-md);
    color: var(--accent-color);
    font-weight: 600;
    font-size: 0.9rem;
    white-space: nowrap;
  }
}

.attributes-panel {
  margin: var(--spacing-2xl) 0 0;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-lg);
  overflow: hidden;
  box-shadow: var(--shadow-sm);

  .panel-header {
    padding: var(--spacing-md) var(--spacing-lg);
    background: linear-gradient(to bottom, var(--primary-bg), var(--card-bg));
    border-bottom: 1px solid var(--border-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: var(--spacing-md);

    h3 {
      margin: 0;
      color: var(--title-color);
      font-size: 1.2rem;
      font-weight: 700;
    }

    .ap-info {
      font-size: 1rem;
      color: var(--text-color);

      strong {
        color: var(--accent-color);
        font-size: 1.2rem;

        &.error {
          color: var(--error-color);
        }

        &.success {
          color: var(--success-color);
        }
      }
    }
  }

  .panel-content {
    padding: var(--spacing-lg);
  }

  .attributes-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: var(--spacing-lg);
    margin-bottom: var(--spacing-lg);
  }

  .attribute-item {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-sm);
  }

  .attribute-display {
    font-size: 0.8rem;
    color: var(--text-color-secondary);
    text-align: center;
    padding: var(--spacing-xs) 0;
    background: var(--primary-bg);
    border-radius: var(--radius-sm);
    border: 1px solid var(--border-color);

    .dim {
      opacity: 0.7;
    }

    .final-value {
      color: var(--accent-color);
      font-weight: bold;
    }
  }

  .status-message {
    padding: var(--spacing-md);
    border-radius: var(--radius-md);
    text-align: center;
    font-weight: 600;
    font-size: 1rem;
    border: 1px solid;

    &.error {
      background: rgba(211, 47, 47, 0.1);
      color: var(--error-color);
      border-color: var(--error-color);
    }

    &.success {
      background: rgba(56, 142, 60, 0.1);
      color: var(--success-color);
      border-color: var(--success-color);
    }

    &.info {
      background: rgba(212, 175, 55, 0.1);
      color: var(--accent-color);
      border-color: var(--accent-color);
    }
  }
}

// 响应式设计
@media (max-width: 768px) {
  .form-row {
    grid-template-columns: 1fr;

    &.full-width {
      grid-template-columns: 1fr;
    }
  }

  .level-input-group {
    flex-direction: column;
    align-items: stretch;
  }

  .location-field {
    .location-options {
      max-height: 300px;
    }

    .location-option {
      .location-label {
        font-size: 0.85rem;
      }
    }
  }

  .attributes-panel {
    .panel-header {
      flex-direction: column;
      align-items: stretch;
    }

    .attributes-grid {
      grid-template-columns: 1fr;
    }
  }
}
