
.form-stepper {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

.stepper-label {
  font-size: 1rem;
  font-weight: 600;
  color: var(--title-color);
  min-width: 60px;
}

.stepper-controls {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  background: var(--input-bg);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  padding: var(--spacing-xs);
}

.stepper-btn {
  width: 32px;
  height: 32px;
  border: 1px solid var(--border-color);
  background: var(--button-bg);
  color: var(--text-color);
  border-radius: var(--radius-sm);
  font-size: 1.2rem;
  font-weight: 700;
  cursor: pointer;
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;

  &:hover:not(:disabled) {
    background: var(--button-hover);
    border-color: var(--accent-color);
    color: var(--accent-color);
  }

  &:active:not(:disabled) {
    transform: scale(0.95);
  }

  &:disabled {
    opacity: 0.4;
    cursor: not-allowed;
    background: var(--border-color-light);
  }

  &.decrement {
    &:hover:not(:disabled) {
      color: var(--error-color);
      border-color: var(--error-color);
    }
  }

  &.increment {
    &:hover:not(:disabled) {
      color: var(--success-color);
      border-color: var(--success-color);
    }
  }
}

.stepper-value {
  min-width: 40px;
  text-align: center;
  font-weight: 600;
  font-size: 1.1rem;
  color: var(--text-color);
  user-select: none;
}
