
.item-card {
  background: var(--card-bg);
  border: 2px solid var(--border-color);
  border-radius: var(--radius-lg);
  padding: var(--spacing-md);
  cursor: pointer;
  transition: all var(--transition-normal);
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: var(--rarity-color);
    opacity: 0.6;
  }

  &:hover:not(.is-disabled) {
    border-color: var(--accent-color);
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
  }

  &.is-selected {
    border-color: var(--accent-color);
    background: linear-gradient(to bottom, var(--card-bg), rgba(212, 175, 55, 0.05));
    box-shadow: 0 0 0 2px rgba(212, 175, 55, 0.2);

    &::before {
      height: 4px;
      opacity: 1;
    }
  }

  &.is-disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-sm);
  padding-bottom: var(--spacing-sm);
  border-bottom: 1px solid var(--border-color-light);

  .item-name {
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--title-color);
  }

  .item-rarity {
    font-size: 0.9rem;
    font-weight: 600;
    padding: 2px 8px;
    border-radius: var(--radius-sm);
    background: rgba(0, 0, 0, 0.1);
  }
}

.card-body {
  margin-bottom: var(--spacing-md);

  .item-info {
    display: flex;
    gap: var(--spacing-xs);
    margin-bottom: var(--spacing-xs);
    font-size: 0.9rem;

    .info-label {
      color: var(--text-light);
      font-weight: 600;
      min-width: 50px;
    }

    .info-value {
      color: var(--text-color);
      flex: 1;

      &.tag {
        font-family: monospace;
        font-size: 0.85rem;
        color: var(--accent-color);
      }

      &.quantity {
        font-family: monospace;
        font-size: 0.9rem;
        font-weight: 600;
        color: #4caf50;
      }

      &.consume {
        font-family: monospace;
        font-size: 0.85rem;
        color: #2196f3;
      }
    }
  }

  .item-effect {
    margin: var(--spacing-sm) 0;
    padding: var(--spacing-sm);
    background: rgba(212, 175, 55, 0.05);
    border-left: 3px solid var(--accent-color);
    border-radius: var(--radius-sm);

    .effect-label {
      font-size: 0.9rem;
      font-weight: 600;
      color: var(--accent-color);
      margin-bottom: var(--spacing-xs);
    }

    .effect-content {
      font-size: 0.85rem;
      line-height: 1.5;
      color: var(--text-color);
    }
  }

  .item-description {
    margin-top: var(--spacing-sm);
    font-size: 0.85rem;
    line-height: 1.6;
    color: var(--text-light);
    font-style: italic;
  }
}

.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: var(--spacing-sm);
  border-top: 1px solid var(--border-color-light);

  .item-cost {
    display: flex;
    gap: var(--spacing-xs);
    align-items: center;

    .cost-label {
      font-size: 0.9rem;
      color: var(--text-light);
    }

    .cost-value {
      font-size: 1.1rem;
      font-weight: 700;
      color: var(--accent-color);
    }
  }

  .selected-badge {
    padding: 4px 12px;
    background: var(--accent-color);
    color: var(--primary-bg);
    border-radius: var(--radius-md);
    font-size: 0.85rem;
    font-weight: 600;
  }
}

// 响应式设计
@media (max-width: 768px) {
  .item-card {
    padding: var(--spacing-sm);
  }

  .card-header {
    .item-name {
      font-size: 1rem;
    }

    .item-rarity {
      font-size: 0.8rem;
    }
  }

  .card-body {
    .item-info {
      font-size: 0.85rem;
    }

    .item-effect {
      .effect-label {
        font-size: 0.85rem;
      }

      .effect-content {
        font-size: 0.8rem;
      }
    }

    .item-description {
      font-size: 0.8rem;
    }
  }

  .card-footer {
    .item-cost {
      .cost-label {
        font-size: 0.85rem;
      }

      .cost-value {
        font-size: 1rem;
      }
    }

    .selected-badge {
      font-size: 0.8rem;
      padding: 3px 10px;
    }
  }
}
